import "./About.css";
import ScrollTrigger from "react-scroll-trigger";

export default function About(props) {
  const handleEnter = () => {
    if (props.currentColors) {
      props.setPreviousColors(props.currentColors);
    } else {
      // props.setPreviousColors({top: '#999', bottom: '#191919'});
      props.setPreviousColors({top: "#405eaa", bottom: "#3C3B6F"});
    };
    props.setCurrentColors({top: "#405eaa", bottom: "#3C386F"});
    props.setCurrentSection("about");
  };

  return (
    <div className="About h-full h-smol md:h-big text-center">
      <div className="max-w-screen-lg w-4/5 mx-auto h-full flex flex-col justify-items-center justify-center">
        <div className="z-10 text-2xl">
          <i>
            하나님께서 디자인 하신 결혼을 이해하고,
            <br /> 잘 준비할 수 있도록 돕는 크리스천 결혼학교에 싱글 여러분을
            초대합니다.
          </i>
        </div>
        <ScrollTrigger onEnter={handleEnter}>
          <div></div>
        </ScrollTrigger>
      </div>
    </div>
  );
}
